<template>
  <div class="home">
    <el-carousel :interval="5000" arrow="always" height="400px">
        <el-carousel-item v-for="item in 4" :key="item">
        <p></p>
        <h2></h2>
        <h3></h3>
        </el-carousel-item>
    </el-carousel>
    <dao :nwxk="this.dao11"></dao>

  </div>
</template>

<script>
import dao from "~/components/dao.vue";
import { dao1 } from "~/data/index";

export default {
  data() {
    return {
      dao11: dao1 ,
    };
  },
  components: {
    dao
  }
};
</script>

<style scoped lang="scss">
.home {
  width: 90%;
  margin: 0 auto;
}

.el-carousel__item {
  color: #242299;
  text-align: left;
}

.el-carousel__item p {
  font-size: 17px;
  padding: 80px 50px 10px 100px;
  letter-spacing: 1px;
  font-weight: 100;
}

.el-carousel__item h2 {
  font-size: 45px;
  width: 740px;
  padding: 0 50px 10px 100px;
  letter-spacing: 1px;
}

.el-carousel__item h3 {
  font-size: 16px;
  width: 400px;
  padding: 0 50px 10px 100px;
  letter-spacing: 1px;
  line-height: 27px;
}

.el-carousel__item:nth-child(2n) {
  background-color: #e4e166;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #e4e166;
}


</style>



