<template>
  <div class="dao">
    <div v-for="item in nwxk" :key="item.label" class="url-top">
        <h3 class="url-title">{{ item.label }}</h3>
        <div>
          <div class="url-box"  v-for="item1 in item.children" :key="item1.label">
            <a :href="item1.router" target="_blank">
              <p class="url-title-1">{{ item1.title }}</p>
              <div class="url-text">
                <p class="url-text-1">{{ item1.text }}</p>
              </div>
            </a>
          </div>
        </div>
    </div>
    <div class="end">
      <p> </p>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      show: true
    };
  },
  methods: {},
  props: ["nwxk"]
};
</script>

<style scoped lang="scss">
.url-top h3 {
  background: #242299;
  color: #fff;
  margin: 20px 0;
  padding: 15px;
  padding-left: 50px;
  font-weight: 200;
  font-size: 28px;
}

.url-box {
  height: 40px;
  background: #fff;
  width: 19.2%;
  display: inline-block;
  margin-right: 1%;
  margin-bottom: 20px;
  transition: 0.4s;
  box-shadow: 0 4px 8px 0 rgba(7, 17, 27, 0.02);
  position: relative;
  float: left;
}

.url-box:nth-child(5n) {
  margin-right: 0;
}

.url-top:after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}

.url-title-1 {
  background: #fff;
  color: #242299;
  line-height: 40px;
  font-size: 18px;
  font-weight: 300;
  text-align: center;
}

.url-text {
  color: #242299;
  width: 100%;
  height: 0;
  overflow: hidden;
  position: absolute;
  top: 120px;
  left: 0;
  background: #fff;
  z-index: 100;
  transition: 0.3s;
}

.url-text-1 {
  padding: 15px;
}

.url-box:hover .url-text {
  height: 180px;
  top: 40px;
  box-shadow: 0 8px 16px 0 rgba(7, 17, 27, 0.2);
}

.url-box:hover .url-title-1 {
  background: #242299;
  transition: 0.2s;
  color: #fff;
}

.end p{
      color: #fff;
    text-align: center;
    line-height: 300px;
    font-size: 40px;
    font-weight: 100;
    background: #242299;
    letter-spacing: 1px;
    margin-top: 80px;
}
</style>



