import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import App from './App.vue'
import router from './router'

// css
import './assets/css/delay.css'
import './assets/css/boot.scss'

Vue.use(ElementUI)

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



